.nav-bar {
	width: 100vw;
	height: 8vh;
	z-index: 1;
	position: absolute;
	top: 0;

	background-color: transparent;
}
.navbar-container {
	width: 90%;
	height: 100%;
	align-items: center;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
}
.nav-logo {
	font-size: 24px;
	text-transform: uppercase;
	letter-spacing: 2px;
}
.nav-links {
	display: flex;
	width: 60%;
	align-items: center;
	height: 100%;
	gap: 5rem;
	justify-content: flex-end;
}
.link {
	letter-spacing: 1.2px;
	color: #eeeeee;
	font-size: 18px;
	cursor: pointer;
	transition: all 0.2s;
}
.link:hover {
	color: #fd8c04;
	transform: scale(1.2);
}
.homePage {
	width: 100vw;
	height: 100vh;
	background: #222831;
}
.halfBg {
	float: right;
	height: 100%;
	background-position: center;
	object-fit: cover;
	width: 60%;
	opacity: 0.6;
}
.homePage__navigation {
	display: flex;
	width: 98vw;
	height: 8vh;
	position: absolute;

	top: 0;
	right: 0%;

	align-items: center;
	justify-content: space-between;
}
.nav-logo {
	font-family: "Texturina", serif;
	font-size: 24px;
	color: #f8f8f8;
	display: flex;
	flex-direction: column;
	justify-content: center;
	text-align: left;
	letter-spacing: 2px;
}
.nav-logo::after {
	content: "Simple";
	font-size: 12px;
	letter-spacing: 1.1px;
	margin-top: -5px;
}
.socialMedia__container {
	width: 10%;
	height: 100%;
	background-color: #6357ff;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
}
.socialMedia__container > a {
	color: #f8f8f8;
	font-size: 20px;
	transition: all 0.3s;
}
.socialMedia__container > a:hover {
	color: black;
}
.homePage__main {
	height: 100%;
	align-items: center;
	display: flex;
	width: 40%;
	justify-content: center;
}
.main__Contents {
	display: flex;
	flex-direction: column;
	width: 80%;
	color: #f8f8f8;
	text-align: left;
	font-family: "Poppins", sans-serif;
	height: 80%;
	justify-content: center;
	gap: 1.3rem;
}
.content__intro {
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-weight: 400;
}
.content__title {
	width: 100%;
	font-size: 30px;
	font-weight: 500;
}
.content__description {
	font-size: 20px;
	word-spacing: 1px;
	line-break: 15px;
}
.getStarted {
	width: 40%;
	height: 60px;
	font-weight: 400;
	font-family: "Poppins", sans-serif;
	letter-spacing: 2px;
	border-radius: 4px;
	background-color: #f8f8f8;
	font-size: 16px;
	outline: none;
	cursor: pointer;
	border: 2px solid transparent;
	color: #222831;
	transition: all 0.3s ease-in-out;
}

.getStarted:hover {
	background-color: transparent;
	border: 2px solid #f8f8f8;
	color: #f8f8f8;
}

@media screen and (max-width: 1000px) {
	.halfBg {
		width: 100%;
		z-index: 1;
		pointer-events: none;
		opacity: 0.1;
		position: absolute;
	}

	.socialMedia__container {
		flex: 0.3;
	}
	.homePage__main {
		width: 100%;
		height: 100%;
	}
	.main__Contents {
		max-height: 100%;
		height: 70%;
		justify-content: center;
		gap: 1rem;
	}
	.content__intro {
		max-width: 100%;
		height: 10%;
	}
	.content__title {
		max-width: 100%;
		height: 60%;
		font-size: 50px;
	}
	.content__description {
		max-width: 100%;
		max-height: 30%;
		font-size: 16px;
	}
}
@media screen and (max-width: 500px) {
	.socialMedia__container {
		display: none;
	}
}
.video-header {
	text-align: center;
	font-size: 40px;
	font-weight: 400;
	color: #eeeeee;
	height: 12vh;
	letter-spacing: 1.2px;
	font-family: "Poppins", sans-serif;
}
.video {
	display: flex;
	width: 100vw;
	height: 100vh;
	flex-direction: column;
	justify-content: center;
	gap: 3rem;
	align-items: center;
	background-color: #393e46;
}
.video-container {
	display: flex;
	justify-content: center;
	gap: 1rem;
	width: 80vw;
	gap: 2rem;
	height: 60vh;
	margin: 0 auto;
}
.frame {
	border-radius: 4px;
	border: 2px solid white;
}
.video-description {
	color: #eeeeee;
	font-size: 18px;
	width: 80%;
	height: 40vh;
	margin: 0 auto;
}
